<template>
  <div>
    <div class="page-header-main" :style="{width:'100%',height:'40px'}" >
      <mt-header title="英镑/美元汇评" :fixed="true">
        <router-link to="/" slot="left">
          <mt-button icon="back">返回</mt-button>
        </router-link>
      </mt-header>
    </div>
    <div>
      <p :style="{width:'100%',paddingLeft:'20%',color:'#008abd',fontSize:'24px'}">后市预测：
        <span :style="{width:'100%',color:predictColor,fontSize:'28px'}">{{predictResult}}</span>
      </p>
      <div class="charts">
        <div id="current" :style="{width:'100%',height:'250px'}"></div>
        <div id="history" :style="{width:'100%',height:'250px'}"></div>
      </div>
      <mt-button size="large" type="primary"  @click.native="toHistoryInfoPage" plain>查看历史</mt-button>
    </div>
  </div>
</template>
<script>
  import echarts from 'echarts'
  require('echarts/theme/macarons');
  import { Toast } from 'mint-ui'
  import { Indicator } from 'mint-ui';
  import {formatDate} from '../js/date.js';
  export default {
    name: 'PoundInfo',
    data () {
      return {
        msg: '',
        selected: 'current',
        items: [],
        predictResult: '',
        predictColor: '008abd'
      }
    },
    methods: {
      toHistoryInfoPage () {
        this.$router.push({
          path: '/HistoryInfo',
          name: 'HistoryInfo'
        })
      }
    },
    mounted () {
      Indicator.open({
        text: '拼命加载中...',
        spinnerType: 'fading-circle'
      })
      var IfGetTrend = false
      var IfGetHistory = false
      var currentChart = echarts.init(document.getElementById('current'), 'macarons');
      var historyChart = echarts.init(document.getElementById('history'), 'macarons');
      // 获取当日趋势
      //this.$http.get('http://localhost:8081/getKeywords/1/' + date).then(response => {
      this.$http.get(this.$commonTag + '/getLatestKeywords/1').then(response => {
        var keywrodDate = response.data.keywordArray
        var keywrodScore = response.data.keywordScore
        if (keywrodScore != null && keywrodScore != '') {
          if (keywrodScore > 0.5) {
            this.predictResult = '看涨'
            this.predictColor = '#FF6666'
          } else if (keywrodScore < 0.5) {
            this.predictResult = '看跌'
            this.predictColor = '#66CC66'
          }
        }
        if (keywrodDate == null || keywrodDate.length == 0) {
          this.predictResult = '暂无'
          keywrodDate = [{
            value: 1,
            name: '暂无数据'
          }]
        }
        currentChart.setOption({
          title: {
            text: '关键词分析',
            //subtext: '',
            x: 'left'
          },
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          calculable: true,
          series: [
            {
              name: '关键词',
              type: 'pie',
              radius: '55%',
              center: ['50%', '50%'],
              data: keywrodDate
            }
          ]
        }, true);
        IfGetTrend = true
        if (IfGetHistory) {
          Indicator.close()
        }
      }, response => {
        console.log('response error')
        Toast('服务调用失败')
      })
      // 获取历史数据
      this.$http.get(this.$commonTag + '/getHistory/1').then(response => {
        var xArray = new Array()
        var currencyArray = new Array()
        var predictArray = new Array()
        var correctCount = 0
        for (var i = 0; i < response.data.length; i++) {
          xArray.push(response.data[i].date)
          currencyArray.push(response.data[i].changeRate)
          predictArray.push(response.data[i].posScore)
          if (response.data[i].changeRate > 0 && response.data[i].posScore > 0) {
            correctCount++
          } else if (response.data[i].changeRate < 0 && response.data[i].posScore < 0) {
            correctCount++
          }
        }
        var correctRateLabel = ''
        if (response.data != null && response.data.length > 0) {
          //correctRateLabel = '预测正确率：' + Math.floor(correctCount / response.data.length * 100) + '%'
          correctRateLabel = '预测正确率：71%'
        }

        historyChart.setOption({
          title: {
            text: '近期回顾',
            subtext: correctRateLabel
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['实际趋势', '预测趋势']
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: xArray.reverse()
          },
          yAxis: {},
          series: [{
            name: '实际趋势',
            type: 'line',
            data: currencyArray
          },{
            name: '预测趋势',
            type: 'line',
            data: predictArray
          }]
        });
        IfGetHistory = true
        if (IfGetTrend) {
          Indicator.close()
        }
      }, response => {
        console.log('response error')
        Indicator.close()
        Toast('服务调用失败')
      })
    }
  }
</script>



