<template>
  <div>
    <div class="page-header-main" :style="{width:'100%',height:'40px'}" >
      <mt-header title="历史汇评" :fixed="true">
        <router-link to="/GBPUSDInfo" slot="left">
          <mt-button icon="back">返回</mt-button>
        </router-link>
      </mt-header>
    </div>
    <div>
      <mt-cell
        title="选择日期："
        @click.native="openPicker"
        is-link
        :value=date>
      </mt-cell>
        <mt-datetime-picker
          ref="picker"
          type="date"
          year-format="{value} 年"
          month-format="{value} 月"
          date-format="{value} 日"
          v-model="pickerValue"
          :start-date = startDate
          :end-date = endDate
          @confirm="handleConfirm">
        </mt-datetime-picker>


          <div class="charts">
            <div id="current" :style="{width:'100%',height:'300px'}"></div>
          </div>
          <mt-cell title="开盘价格" :value=startPrice></mt-cell>
          <mt-cell title="收盘价格" :value=endPrice></mt-cell>
          <mt-cell title="当日最高价格" :value=topPrice></mt-cell>
          <mt-cell title="当日最低价格" :value=bottomPrice></mt-cell>
          <mt-cell title="当日实际趋势" :value=changeRate></mt-cell>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  require('echarts/theme/macarons');
  import {Toast} from 'mint-ui'
  import {Indicator} from 'mint-ui';
  import {formatDate} from '../js/date.js';
  export default {
    name: 'PoundInfo',
    data () {
      return {
        currentChart: null,
        msg: '',
        selected: 'current',
        items:[],
        date: formatDate(new Date(new Date().getTime() - 24*60*60*1000), 'yyyy/MM/dd'),
        pickerValue: new Date(new Date().getTime() - 24*60*60*1000),
        startDate: new Date('2017/01/01'),
        endDate: new Date(new Date().getTime() - 24*60*60*1000),
        startPrice: '',
        endPrice: '',
        topPrice: '',
        bottomPrice: '',
        changeRate: ''
      }
    },
    methods: {
      toPageView (links) {
        this.$router.push({
          path: '/PageView',
          name: 'PageView',
          params: {
            links: links
          }
        })
      },
      openPicker () {
        this.$refs.picker.open()
      },
      handleConfirm () {
        this.date = formatDate(this.pickerValue, 'yyyy-MM-dd')
        this.getKeyword()
      },
      getKeyword () {
        Indicator.open({
          text: '拼命加载中...',
          spinnerType: 'fading-circle'
        })
        // 获取当日趋势
        this.$http.get(this.$commonTag + '/getHistoryCurrencyInfo/1/' + this.date).then(response => {
          if (response.data != null && response.data[0] != null) {
            if (response.data[0].changeRate != 0) {
              this.startPrice = response.data[0].startPrice
              this.endPrice = response.data[0].endPrice
              this.topPrice = response.data[0].topPrice
              this.bottomPrice = response.data[0].bottomPrice
              if (response.data[0].changeRate > 0) {
                this.changeRate = '+' + response.data[0].changeRate + '%'
              } else {
                this.changeRate = response.data[0].changeRate + '%'
              }
            } else {
              this.startPrice = '该日休市'
              this.endPrice = ''
              this.topPrice = ''
              this.bottomPrice = ''
              this.changeRate = ''
            }
          } else {
            this.startPrice = '该日休市'
            this.endPrice = ''
            this.topPrice = ''
            this.bottomPrice = ''
            this.changeRate = ''
          }
        }, response => {
          console.log('response error')
          Toast('服务调用失败')
        })
        // 获取当日关键词信息
        this.$http.get(this.$commonTag + '/getKeywords/1/' + this.date).then(response => {
          var keywrodDate = response.data.keywordArray
          var keywrodScore = response.data.keywordScore
          var keywrodScoreLabel = ''
          if (keywrodScore != null && keywrodScore != '') {
            if (keywrodScore > 0.5) {
              keywrodScoreLabel = '预测结果：看涨'
            } else if (keywrodScore < 0.5) {
              keywrodScoreLabel = '预测结果：看跌'
            }
          }
          if (keywrodDate == null || keywrodDate.length == 0) {
            keywrodScoreLabel = ''
            keywrodDate = [{
              value: 1,
              name: '暂无数据'
            }]
          }
          this.currentChart.setOption({
            title: {
              text: '关键词分析',
              subtext: keywrodScoreLabel,
              x: 'left'
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            calculable: true,
            series: [
              {
                name: '关键词',
                type: 'pie',
                radius: '55%',
                center: ['50%', '50%'],
                data: keywrodDate
              }
            ]
          }, true);
          Indicator.close()
        }, response => {
          console.log('response error')
          Toast('服务调用失败')
        })
      }
    },
    mounted () {
      this.openPicker()
      this.currentChart = echarts.init(document.getElementById('current'), 'macarons')
    }
  }
</script>



