import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import App from './App.vue'
import router from './router'
import echarts from 'echarts'
import axios from 'axios'

Vue.prototype.$http = axios
//Vue.prototype.$commonTag = 'https://211.159.185.85'
//Vue.prototype.$commonTag = 'http://localhost:8081'
Vue.prototype.$commonTag = ''
Vue.use(MintUI)
Vue.use(echarts)

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



