<template>
  <div>
    <div class="page-header-main" :style="{width:'100%',height:'40px'}" >
      <mt-header title="智能汇评" :fixed="true">
      </mt-header>
    </div>
    <div>
      <div class="charts">
        <div id="site" :style="{width:'100%',height:'280px'}"></div>
      </div>
      <p class="page-picker-desc" :style="{width:'100%',paddingLeft:'10px',color:'#008abd',fontSize:'18px'}">选择币种：</p>
      <div class="page-picker-wrapper">
        <mt-picker :slots="currencySlot" @change="onCurrencyChange" :visible-item-count="3"></mt-picker>
      </div>
      <mt-cell title="当前行情" >
        <mt-badge size="large" :style="{backgroundColor:currencyColor}">{{ currencyPrice }}</mt-badge>
      </mt-cell>
      <mt-cell title="当日涨幅">
        <mt-badge size="large" :style="{backgroundColor:currencyColor}">{{ currencyTrend }}</mt-badge>
      </mt-cell>
      <mt-button size="large" type="primary" @click.native="noServiceTips" plain>开始智能预测</mt-button>
    </div>
  </div>

</template>
<script>
  import echarts from 'echarts'
  import {Toast,Indicator} from 'mint-ui'
  require('echarts/theme/macarons');
  require('echarts-wordcloud');
  export default {
    name: 'Index',
    data () {
      return {
        currencyInfo: '/GBPUSDInfo',
        currencyPrice: '',
        currencyTrend: '',
        currencyColor: '',

        currency: '英镑/美元',
        number: 0,
        currencySlot: [{
          flex: 1,
          values: ['美元/人民币', '英镑/美元', '欧元/美元', '美元/日元', '澳元/美元'],
          className: 'slot1',
          defaultIndex:  1,
          value: '英镑/美元'
        }],
      }
    },
    methods: {
      noServiceTips: function (event) {
        if (this.currency != '英镑/美元'){
          Toast('整理机器学习训练数据是个体力活，暂时只开发了英镑/美元T T，敬请期待: )')
        } else {
          this.$router.push({
            path: '/GBPUSDInfo',
            name: 'GBPUSDInfo'
          })
        }
      },
      loadWordCloud: function () {
        this.siteChart.setOption({
          title: {
            text: '数据来自：',
            top: 10,
            left: 5
          },
          tooltip: {
            show: true
          },
          animation: true,
          series: [{
            type: 'wordCloud',
            shape: 'circle',
            left: 'center',
            top: 'center',
            width: '80%',
            height: '80%',
            right: null,
            bottom: null,
            sizeRange: [8, 36],
            rotationRange: [-90, 90],
            rotationStep: 45,
            gridSize: 8,
            drawOutOfBound: false,
            // Global text style
            textStyle: {
              normal: {
                fontFamily: 'sans-serif',
                fontWeight: 'bold',
                // Color can be a callback function or a color string
                color: function () {
                  // Random color
                  return 'rgb(' + [
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160),
                    Math.round(Math.random() * 160)
                  ].join(',') + ')';
                }
              },
              emphasis: {
                shadowBlur: 10,
                shadowColor: '#333'
              }
            },

            data: [
              {
                name: "网易外汇",
                value: 36
              },
              {
                name: "中国外汇网",
                value: 48
              },
              {
                name: "亚洲外汇网",
                value: 35
              },
              {
                name: "和讯外汇网",
                value: 57
              },
              {
                name: "startUrl5",
                value: 21
              },
              {
                name: "FX168",
                value: 11
              },
              {
                name: "外汇通",
                value: 31
              },
              {
                name: "外汇之星",
                value: 14
              },
              {
                name: "外汇之声",
                value: 14
              },
              {
                name: "厚元咨询",
                value: 25
              },
              {
                name: "田洪良外汇网",
                value: 14
              },
              {
                name: "凤凰外汇",
                value: 30
              },
              {
                name: "汇通网",
                value: 21
              },
              {
                name: "外汇通",
                value: 15
              },
              {
                name: "外汇110网",
                value: 11
              },
              {
                name: "腾讯外汇网",
                value: 41
              },
              {
                name: "新浪外汇网",
                value: 35
              },
              {
                name: "全景外汇",
                value: 15
              },
              {
                name: "亚汇网",
                value: 20
              },
              {
                name: "汇丰外汇网",
                value: 13
              },
              {
                name: "外汇112网",
                value: 15
              }
            ]
          }]
        }, true);
      },
      onCurrencyChange: function(picker, values) {
        this.currency = values[0]
        switch (this.currency) {
          case '英镑/美元':
            Indicator.open({
              text: '拼命加载中...',
              spinnerType: 'fading-circle'
            })
            this.$http.get(this.$commonTag + '/getRealtimeInfo').then(response => {
              var data = response.data
              this.currencyPrice = response.data[0].endPrice
              this.currencyTrend = response.data[0].changeRate
              if (this.currencyTrend > 0) {
                this.currencyColor = '#FF6666'
                this.currencyTrend = '+' + this.currencyTrend+ '%'
              } else if (this.currencyTrend < 0) {
                this.currencyColor = '#66CC66'
                this.currencyTrend = response.data[0].changeRate + '%'
              }
              Indicator.close()
            }, response => {
              console.log('response error')
              Indicator.close()
              Toast('服务调用失败')
            })
            break
           // 美元/人民币', '英镑/美元', '欧元/美元', '美元/日元', '澳元/美元'],
          case '美元/人民币':
            this.currencyPrice = '6.3108'
            this.currencyTrend = '-0.18%'
            this.currencyColor = '#66CC66'
            break
          case '欧元/美元':
            this.currencyPrice = '1.2276'
            this.currencyTrend = '-0.24%'
            this.currencyColor = '#66CC66'
            break
          case '美元/日元':
            this.currencyPrice = '109.0500'
            this.currencyTrend = '+0.29%'
            this.currencyColor = '#FF6666'
            break
          case '澳元/美元':
            this.currencyPrice = '0.7793'
            this.currencyTrend = '-0.17%'
            this.currencyColor = '#66CC66'
            break
          default:
            this.currencyPrice = ''
            this.currencyTrend = ''
            this.currencyColor = '#FF6666'
        }
      }
    },
    created () {
    },
    mounted () {
      this.siteChart = echarts.init(document.getElementById('site'), 'macarons')
      this.loadWordCloud()
    }
  }
</script>



