<template>
  <div>
    <mt-header title="欧元/美元汇评">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="charts">
      <div id="current" :style="{width:'100%',height:'300px'}"></div>
      <div id="history" :style="{width:'100%',height:'300px'}"></div>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import { Toast } from 'mint-ui'
  import { Indicator } from 'mint-ui';
  export default {
    name: 'HelloWorld',
    data () {
      return {
        msg: '',
      }
    },
    mounted () {
      Indicator.open({
        text: '拼命加载中...',
        spinnerType: 'fading-circle'
      })
      var IfGetTrend = false
      var IfGetHistory = false
      // 获取当日趋势
      this.$http.get('http://localhost:8081/getTrend/2').then(response => {
        var currentChart = echarts.init(document.getElementById('current'));
        var posScore = '50'

        if(response.data.length>0){
          posScore = response.data[0].pos_score
        }
        currentChart.setOption({
          title: { text: '欧元/美元'},
          tooltip : {
            formatter: "{a} <br/>{b} : {c}%"
          },
          toolbox: {
            show : false
          },
          series : [
            {
              name:'指数',
              type:'gauge',
              startAngle: 180,
              endAngle: 0,
              center : ['50%', '80%'],    // 默认全局居中
              radius : 160,
              axisLine: {            // 坐标轴线
                lineStyle: {       // 属性lineStyle控制线条样式
                  color: [[0.5, 'lightgreen'],[1, '#ff4500']],
                  width: 50
                }
              },
              axisTick: {            // 坐标轴小标记
                show: false,
                splitNumber: 10,   // 每份split细分多少段
                length :12,        // 属性length控制线长
              },
              axisLabel: {           // 坐标轴文本标签，详见axis.axisLabel
                formatter: function(v){
                  switch (v+''){
                    case '10': return '跌';
                    case '90': return '涨';
                    default: return '';
                  }
                },
                textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                  color: '#fff',
                  fontSize: 12,
                  fontWeight: 'bolder'
                }
              },
              splitLine: {           // 分隔线
                show: false
              },
              pointer: {
                width:5,
                length: '90%'
              },
              title : {
                show : true,
                offsetCenter: [0, '-125%'],       // x, y，单位px
                textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                  color: '#222',
                  fontSize: 15
                }
              },
              detail : {
                show : true,
                backgroundColor: 'rgba(0,0,0,0)',
                borderWidth: 0,
                borderColor: '#ccc',
                width: 100,
                height: 40,
                offsetCenter: [0, -50],       // x, y，单位px
                formatter:'{value}%',
                textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                  fontSize : 30
                }
              },
              data:[{value: posScore, name: '当前预测'}]
            }
          ]
        },true);
        IfGetTrend = true
        if (IfGetHistory) {
          Indicator.close()
        }
      }, response => {
        console.log('response error')
        Toast('服务调用失败')
      })
      // 获取历史数据
      this.$http.get('http://localhost:8081/getHistory/2').then(response => {
        var historyChart = echarts.init(document.getElementById('history'));
        var xArray = new Array()
        var yArray = new Array()
        for( var i=0;i<response.data.length;i++){
          xArray.push(response.data[i].date)
          yArray.push(response.data[i].changeRate)
        }

        historyChart.setOption({
          title: { text: '历史数据(%)'},
          tooltip: {},
          xAxis: {
            data: xArray.reverse()
          },
          yAxis: {},
          series: [{
            name: '欧元/美元',
            type: 'line',
            data: yArray
          }]
        });
        IfGetHistory= true
        if (IfGetTrend) {
          Indicator.close()
        }
      }, response => {
        console.log('response error')
        Indicator.close()
        Toast('服务调用失败')
      })

    }
  }
</script>



