import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import GBPUSDInfo from '@/components/GBPUSDInfo'
import EURUSDInfo from '@/components/EURUSDInfo'
import PageView from '@/components/PageView'
import HistoryInfo from '@/components/HistoryInfo'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/HistoryInfo',
      name: 'HistoryInfo',
      component: HistoryInfo
    },
    {
      path: '/GBPUSDInfo',
      name: 'GBPUSDInfo',
      component: GBPUSDInfo
    },
    {
      path: '/EURUSDInfo',
      name: 'EURUSDInfo',
      component: EURUSDInfo
    },
    {
      path: '/PageView',
      name: 'PageView',
      component: PageView
    }
  ]
})



